import React, { ButtonHTMLAttributes, DetailedHTMLProps } from 'react'

import s from './SuperButton.module.css'

//    , children        
type DefaultButtonPropsType = DetailedHTMLProps<
  ButtonHTMLAttributes<HTMLButtonElement>,
  HTMLButtonElement
>

type SuperButtonPropsType = DefaultButtonPropsType & {
  xType?: string
}

const SuperButton: React.FC<SuperButtonPropsType> = ({
  xType,
  className,
  disabled,
  ...restProps //       restProps,    children
}) => {
  // const finalClassName = s.button
  //     + (disabled ? ` ${s.disabled}` : ' ') +
  //     (xType === 'red' ? ` ${s.red}` :
  //         xType === 'secondary' ? ' ' + s.secondary :
  //             ' ' + s.default)
  //let finalClassName = ''

  const finalClassNameFunc = () => {
    let a = s.button

    if (disabled) {
      a = `${a} ${s.disabled}`
    }

    if (xType === 'red') {
      a = `${a} ${s.red}`
    } else if (xType === 'secondary') {
      a = `${a} ${s.secondary}`
    } else {
      a = `${a} ${s.default}`
    }

    return a
  }
  //const finalClassName = s.button + if(disabled) {

  // const fclass = `${s.button} ${disabled ? s.disabled : ''} ${xType === 'red' ? s.red : ''} ${xType === 'secondary' ? ' ' + s.secondary : ''} ${s.default}`

  //    
  // + (disabled
  // ? ...
  // : xType === 'red'
  //     ? ...
  // + (className ? ' ' + className : '') //    

  return (
    <button
      disabled={disabled}
      className={finalClassNameFunc()}
      {...restProps} //        (children  )
    />
  )
}

export default SuperButton
