import React from 'react'

import ExpandLessIcon from '@mui/icons-material/ExpandLess'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import HorizontalRuleIcon from '@mui/icons-material/HorizontalRule'

//      
// const downIcon = '[\\/]'
// const upIcon = '[/\\]'
// const noneIcon = '[--]'

const downIcon = <ExpandMoreIcon />
const upIcon = <ExpandLessIcon />
const noneIcon = <HorizontalRuleIcon />

export type SuperSortPropsType = {
  id?: string
  sort: string
  value: string
  onChange: (newSort: string) => void
}

export const pureChange = (sort: string, down: string, up: string) => {
  //  , sort: (click) => down (click) => up (click) => '' (click) => down ...
  if (sort === '') {
    return down
  } else if (sort === down) {
    return up
  } else if (sort === up) {
    return ''
  } else {
    return down
  }
}

const SuperSort: React.FC<SuperSortPropsType> = ({ sort, value, onChange, id = 'hw15' }) => {
  const up = '0' + value
  const down = '1' + value

  const onChangeCallback = () => {
    onChange(pureChange(sort, down, up))
  }

  let icon: JSX.Element

  if (sort === down) {
    icon = downIcon
  } else {
    icon = sort === up ? upIcon : noneIcon
  }

  // const icon = sort === down ? downIcon : sort === up ? upIcon : noneIcon

  return (
    <span id={id + '-sort-' + value} onClick={onChangeCallback}>
      {/* */}
      {/*<img*/}
      {/*    id={id + '-icon-' + sort}*/}
      {/*    src={icon}*/}
      {/*/>*/}
      {icon}
    </span>
  )
}

export default SuperSort
